.fullscreen-menu {
    position: fixed ;
    top: 0;
    left: 0;
    width: 99vw;
    height: 100vh;
    background-image: url("../../Assets/ultrawide-space-moon-earth-wallpaper-preview.jpg");
    /* background: rgba(0, 0, 0, 0.9); */
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-color: rgba(0, 0, 0, 0.9);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(2px);
    border-radius: 10px;
    color: white;
    transition: transform 0.3s ease-out;
    z-index: 10;
   }
   
   .fullscreen-menu.open {
    transform: translateY(0);
   }
   
   .fullscreen-menu button {
    position: absolute;
    top: 10px;
    right: 10px;
   }

   .color-white {
    color: white;
  }

  .fullscreenmenu {
    display: flex;
    flex-direction: column;
    /* background-color: #282c34; */
    color: black;
    padding: 0 20px;
    height: 15px;
    float : left;
    margin-top: 20rem;
    margin-left: 20px;
  }

  .dropdown {
    position: relative;
    display: inline-block;
   }
  
  .dropdown-button {
    background-color: #4CAF50;
    color: white;
    padding: 16px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    width: 100%;
    text-align: left;
   }

   .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    width: 100%;
    text-align: left;
   }

   .dropdown-content Link {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
   }

   .offcanvas {
      background: linear-gradient(to right, #3494E6, #EC6EAD);
   }

   .button {
    background: transparent !important;
    color: #fff !important;
    font-size: 17px !important;
    text-transform: uppercase !important;
    font-weight: 600  !important;
    border: none  !important;
    padding: 20px 30px !important;
    perspective: 30rem !important;
    border-radius: 10px !important;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.308) !important;
    margin: auto auto !important;
    cursor: pointer !important;
    text-align: center !important;
    height: 50px !important;
  }
  
  .button::before {
    content: '' !important;
    display: block !important;
    position: absolute !important;
    width: 100% !important;
    height: 100% !important;
    top: 0px !important;
    left: 0 !important;
    border-radius: 10px !important;
    background: linear-gradient(320deg, rgba(0, 140, 255, 0.678), rgba(128, 0, 128, 0.308)) !important;
    z-index: 1 !important;
    transition: background 3s !important;
    margin: auto auto !important;
  }
  
  .button:hover::before {
    animation: rotate 1s !important;
    transition: all .5s !important;
  }
  
  @keyframes rotate {
    0% {
      transform: rotateY(180deg);
    }
  
    100% {
      transform: rotateY(360deg);
    }
  }

  .button2-container {
    display: flex;
    background-color: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(50px);
    width: 250px;
    height: 40px;
    align-items: center;
    justify-content: space-around;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px,
          rgba(245, 73, 144, 0.5) 5px 10px 15px;
    position: absolute;
    top: 5rem;
    left: 36%;
  }

  .button1-container {
    display: flex;
    background-color: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(50px);
    width: 250px;
    height: 40px;
    align-items: center;
    justify-content: space-around;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px,
          rgba(245, 73, 144, 0.5) 5px 10px 15px;
    position: absolute;
    top: 5rem;
    left: 15%;
  }
  
  .button2 {
    outline: 0 !important;
    border: 0 !important;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    transition: all ease-in-out 0.3s;
    cursor: pointer;
  }
  
  .button2:hover {
    transform: translateY(-3px);
  }
  
  .icon {
    font-size: 20px;
  }